import { $host } from '.';

export const setLikeCommentHTTP = async function ({
  userId,
  commentId,
  add,
}: commentLikesT): Promise<any> {
  try {
    const { data } = await $host.post(`api/likecomment`, {
      userId,
      commentId,
      add,
    });
    return data;
  } catch (error) {
    return error;
  }
};

export const changeCommentHTTP = async function ({
  commentId,
  text,
}: changeCommitT): Promise<any> {
  try {
    const { data } = await $host.patch(`api/comment`, {
      commentId,
      text,
    });
    return data;
  } catch (error) {
    return error;
  }
};

export const setCommentHTTP = async function ({
  deviceId,
  text,
}: Pick<changeCommitT, 'text'> & { deviceId: string | number }): Promise<any> {
  try {
    const { data } = await $host.post(`api/comment`, {
      text,
      deviceId,
    });
    return data;
  } catch (error) {
    return error;
  }
};

type changeCommitT = Pick<commentLikesT, 'commentId'> & { text: string };

type commentLikesT = {
  userId: number | string;
  commentId: number | string;
  add: boolean;
};
