import { useEffect } from 'react';
import { FieldValues } from 'react-hook-form';
import { POSTS } from './conmopents/POSTS/POSTS';
import { REVIEWS } from './conmopents/REVIEWS/REVIEWS';
import './style/Comment.scss';
import { $ } from '../../components';

export const Comments: React.FC<CommentsConf> = ({
  model,
  elem,
  setElem,
  product,
}) => {
  useEffect(() => {
    window.addEventListener('resize', chagneCourseLineComment);
    window.addEventListener('scroll', chagneCourseLineComment);
    return () => {
      window.removeEventListener('resize', chagneCourseLineComment);
      window.removeEventListener('scroll', chagneCourseLineComment);
    };
  }, []);

  useEffect(() => {
    chagneCourseLineComment();
  });

  const handlerMouse = (e) => {
    const lihad = document.querySelector('.comments__item.check');
    const li = e.target.closest('.comments__item');
    lihad?.classList.remove('had');
    li?.classList.add('had');

    const container =
      (+document.documentElement.clientWidth -
        +(document.querySelector('.container') as HTMLElement).clientWidth) /
      2;

    const line = document.querySelector('.comments__line') as HTMLElement;
    const course =
      li.querySelector('p').getBoundingClientRect().left - container;
    const width = getComputedStyle(li.querySelector('p')).getPropertyValue(
      'width'
    );
    line.style.width = width;
    line.style.transform = `translateX(${course}px)`;
  };

  const handlerClick = (e) => {
    $('.comments__item').remove('check');
    $(e.target.closest('.comments__item')).add('check');
    setElem(() => e.target.closest('p').textContent);
  };

  const element =
    elem == 'DETAILS' ? (
      <Details product={product} />
    ) : elem == 'ADD POST' ? (
      <REVIEWS setElem={setElem} model={model} />
    ) : elem == 'POSTS' ? (
      <POSTS model={model} />
    ) : (
      ''
    );

  return (
    <div className='comments'>
      <div className='comments__wrap'>
        <div onMouseOut={chagneCourseLineComment} className='comments__items'>
          <div onMouseOver={handlerMouse} className='comments__item check had'>
            <p className='big' onClick={handlerClick}>
              DETAILS
            </p>
          </div>
          <div
            onMouseOver={handlerMouse}
            className='comments__item comments__posts'
          >
            <p className='big' onClick={handlerClick}>
              POSTS
            </p>
          </div>
          <div onMouseOver={handlerMouse} className='comments__item'>
            <p className='big' onClick={handlerClick}>
              ADD POST
            </p>
          </div>
        </div>
        <div className='comments__course'>
          <div className='comments__line'></div>
        </div>
        <div className='comments__block'>{element}</div>
      </div>
    </div>
  );
};

export type CommentsConf = {
  setElem: React.Dispatch<React.SetStateAction<string>>;
  elem: string;
  model: (name: string, typeblock: string) => void;
  product?: FieldValues;
};

export default function Details({ product }) {
  return (
    <div className='details'>
      <p className=''>
        {product?.info && product?.info != ''
          ? product.info
          : `Lorem ipsum dolor sit amet consectetur adipisicing elit. Rerum voluptas
		in praesentium cum adipisci pariatur veritatis magni similique,
		repudiandae, aspernatur nemo, ratione necessitatibus aut est non eaque
		dolorum incidunt fugit!`}
      </p>

      <p className=''>
        <strong> Sizes: </strong>
      </p>

      <p className=''> - XS (1-3) </p>

      <p className=''> - Small (3-5) </p>

      <p className=''> - Medium (5-8) </p>

      <p className=''> - Large (8-10) </p>

      <p className=''> - XL (10-13)</p>
    </div>
  );
}

function chagneCourseLineComment() {
  const block = document.querySelector('.comments__item.check');
  const lihad = document.querySelector('.comments__item.had');
  const check = document.querySelector(
    '.comments__item.check p'
  ) as HTMLElement;
  const container =
    (+document.documentElement.clientWidth -
      +(document.querySelector('.container') as HTMLElement).clientWidth) /
    2;

  const line = document.querySelector('.comments__line') as HTMLElement;
  lihad?.classList.remove('had');
  block?.classList.add('had');

  if (block) {
    const width = (
      document.querySelector('.comments__item.check p') as HTMLElement
    ).clientWidth;

    let course = check.getBoundingClientRect().left - container;

    if (+document.documentElement.clientWidth <= 769) {
      course += -10;
    }

    line.style.width = width + 'px';
    line.style.transform = `translateX(${course}px)`;
  } else {
    const width = (document.querySelector('.header__link li p') as HTMLElement)
      .clientWidth;
    const course = check.getBoundingClientRect().left - container;

    line.style.width = width + 'px';
    line.style.transform = `translateX(${course}px)`;
  }
}
