import { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { CommentsConf } from '../../Comments';
import { StarsChange } from '../Start/StarDevice';

import './REVIEWS.scss';
import { $, BtnLoading, Loading } from '../../../../components';
import { useAppDispatch, useAppSelector } from '../../../../store';
import { getAllCommentHTTP } from '../../../../store/commentSlice/commentSlice';
import { changeRatingHTTP } from '../../../../store/goodsSlices/ratingSlice/ratingSlice';
import { setCommentHTTP } from '../../../../store/http/commentHTTP';

export const REVIEWS: React.FC<ReviewTypes> = ({ setElem, model }) => {
  const { comments } = useAppSelector((state) => state.comment);
  const { isLoading } = useAppSelector((state) => state.rating);
  const { user, isAuth } = useAppSelector((state) => state.user);
  const dispatch = useAppDispatch();

  const deviceId = useLocation().pathname.match(/[0-9]*$/gm);
  const [value, setValue] = useState('');
  const [rating, setRating] = useState(0);

  async function fetchingFirst() {
    if (deviceId) {
      await dispatch(getAllCommentHTTP({ id: deviceId[0] }));
    }
  }

  async function setFetching() {
    if (deviceId && 'id' in user) {
      const response = await setCommentHTTP({
        deviceId: deviceId[0],
        text: value,
      });
      await dispatch(
        changeRatingHTTP({
          userId: user.id,
          deviceId: deviceId[0],
          quantity: rating,
        })
      );
      if (response.message == 'Ok') {
        $('.comments__item').remove('check');
        $('.comments__posts').add('check');
        setElem('POSTS');
      }
    }
  }

  useEffect(() => {
    fetchingFirst();
  }, []);

  const handlerSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (isAuth) {
      setFetching();
    } else {
      model('in', 'sing');
    }
  };

  return (
    <div className='reviews'>
      <>
        {comments.filter((item) => 'id' in user && user.id == item.userId)
          .length == 0 ? (
          <form onSubmit={handlerSubmit} className='reviews__form'>
            <legend className='reviews__title'>
              <p className='big'>YOUR REVIEW:</p>
            </legend>
            <div className='reviews__star'>
              <StarsChange
                number={rating}
                setRating={setRating}
                text={'Your score on the product'}
              />
            </div>
            <textarea
              value={value}
              onChange={(e) => {
                setValue(e.target.value);
              }}
              name='text'
            />
            <button className='reviews__btn'>
              {isLoading ? (
                <BtnLoading>
                  <Loading />
                </BtnLoading>
              ) : (
                <p className='big'>SUBMIT REVIEW</p>
              )}
            </button>
          </form>
        ) : (
          <ReviewAllready />
        )}
      </>
    </div>
  );
};

export const ReviewAllready: React.FC = () => {
  return (
    <div className='reviewallready'>
      <div className='reviewallready__text'>
        <h2>You Have Already Comment</h2>
      </div>
    </div>
  );
};

type ReviewTypes = Omit<CommentsConf, 'elem'>;
