import { Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { links } from '../../../../../Router';
import { $ } from '../../../../../components';
import { ProductT } from '../../../../../store/goodsSlices/goods.config';

import 'swiper/css';
import './Slider.scss';
import 'swiper/css/pagination';

export const Slider: React.FC<SliderConf> = ({ product }) => {
  const pagination = {
    clickable: true,
  };

  return (
    <>
      <Swiper
        observer={true}
        observeParents={true}
        pagination={pagination}
        modules={[Pagination]}
        className={
          Array.from(Object.values(JSON.parse(product?.deviceimg.img)))
            .length <= 1
            ? 'SwiperProduct notpagination'
            : 'SwiperProduct'
        }
        allowTouchMove={true}
      >
        {Array.from(Object.values(JSON.parse(product?.deviceimg.img))).map(
          (item, index) => {
            return (
              <SwiperSlide key={index}>
                <div className='product__img load'>
                  <div className='product__load'></div>
                  <img
                    src={`${links.API_URL}${item}`}
                    onLoad={(e) => handleImageLoaded(e, item, '.product__img')}
                  />
                </div>
              </SwiperSlide>
            );
          }
        )}
      </Swiper>
    </>
  );
};

export const handleImageLoaded = (
  e: React.SyntheticEvent<HTMLDivElement>,
  item,
  elem: string
) => {
  const target = e.target as HTMLElement;
  $(
    target.closest(elem) as HTMLElement
  ).take().style.backgroundImage = `url(${links.API_URL}${item})`;
  $(target.closest(elem) as HTMLElement).remove('load');
  target.remove();
  $('.product__table').remove('load');
};

type SliderConf = {
  product: ProductT;
};
