import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { ReactComponent as Up } from '../../../../img/icons/thumbs-up.svg';
import { ReactComponent as Down } from '../../../../img/icons/thumbs-down.svg';
import { ReactComponent as Bucket } from '../../../../img/icons/trash.svg';
import { ReactComponent as Reload } from '../../../../img/icons/reload.svg';
import { Stars } from '../Start/StarDevice';
import { PostsItemReload } from './PostItemReload';
import { CommentsConf } from '../../Comments';
import { TypesConfig, useAppDispatch, useAppSelector } from '../../../../store';
import {
  deleteCommentHTTP,
  getAllCommentHTTP,
} from '../../../../store/commentSlice/commentSlice';
import { $, Loading } from '../../../../components';
import { setLikeCommentHTTP } from '../../../../store/http/commentHTTP';
import { deleteRatingHTTP } from '../../../../store/goodsSlices/ratingSlice/ratingSlice';

import './POSTS.scss';

export const POSTS: React.FC<PostTypes> = ({ model }) => {
  const { comments, isLoading } = useAppSelector((state) => state.comment);
  const dispatch = useAppDispatch();

  const deviceId = useLocation().pathname.match(
    /[0-9]*$/gm
  ) as RegExpMatchArray;

  async function fetching() {
    if (deviceId) {
      await dispatch(getAllCommentHTTP({ id: deviceId[0] }));
    }
  }

  useEffect(() => {
    fetching();
  }, []);

  return (
    <div className='posts'>
      {isLoading ? (
        <Loading name='all' />
      ) : comments.length > 0 ? (
        comments.map((item: TypesConfig) => {
          return (
            <div key={item.id} className='posts__item'>
              <ItemPost
                item={item}
                fetching={fetching}
                model={model}
                deviceId={deviceId}
              />
            </div>
          );
        })
      ) : (
        <div className='posts__notfound'>
          <h2>No Related Posts</h2>
        </div>
      )}
    </div>
  );
};

export const ItemPost: React.FC<PostTypes<ReviewTypes>> = ({
  item,
  model,
  deviceId,
}) => {
  const { rating } = useAppSelector((state) => state.rating);
  const { isAuth, user } = useAppSelector((state) => state.user);
  const dispatch = useAppDispatch();

  const [element, setElement] = useState<TypesConfig>(item);
  const [like, setLike] = useState<TypesConfig>(item.like);
  const [reload, setReload] = useState(false);
  const [ratingValue, setRatingValue] = useState(0);

  const handlerBasket = async function (item) {
    if ('id' in user && Array.isArray(deviceId)) {
      dispatch(
        deleteCommentHTTP({
          userId: item.userId,
          commentId: item.id,
          deviceId: deviceId[0],
        })
      );
      dispatch(deleteRatingHTTP({ userId: user.id, deviceId: deviceId[0] }));
    }
  };

  useEffect(() => {
    if (rating.length > 0) {
      setRatingValue(
        rating.filter((el) => {
          return el.userId == item.userId;
        })[0]?.quantity
      );
    }
  }, []);

  const handlerClick = async (
    add: boolean,
    e: React.MouseEvent<HTMLElement>
  ) => {
    if (isAuth && 'id' in user) {
      const response = await setLikeCommentHTTP({
        commentId: item.id,
        userId: user.id,
        add,
      });

      const target = (e.target as HTMLElement).closest(
        '.posts__like'
      ) as HTMLElement;

      $('.posts__like').remove('add');
      $(target).toggle('add');

      setLike(response);
    } else {
      model('in', 'sing');
    }
  };

  return (
    <>
      {!reload ? (
        <div className='posts__block'>
          <div className='posts__head'>
            <div className='posts__name'>
              <p>{element.user?.firstName}</p>
            </div>
            <div className='posts__date'>
              <p className='mini'>
                {element.date.replace(/(:[0-9]{2})(?=\s[PMAM])/g, '')}
              </p>
            </div>
            <div className='posts__rating'>
              <Stars number={ratingValue || 0} />
            </div>
          </div>

          <div className='posts__text'>
            <p>{element.text}</p>
          </div>
          <div className='posts__icons'>
            <div className='posts__thumbs'>
              <div
                className={
                  like?.likecomments &&
                  like?.likecomments.filter((el) => {
                    return el.userId == element.userId;
                  }).length > 0
                    ? 'add posts__like '
                    : ' posts__like '
                }
              >
                <div
                  onClick={(e) => handlerClick(true, e)}
                  className='posts__icon'
                >
                  <div className='posts__svg'>
                    <Up />
                  </div>
                </div>
                <div className='posts__countlike'>
                  <p>{like.quantityLike}</p>
                </div>
              </div>
              <div
                className={
                  like?.dislikes &&
                  like?.dislikes.filter((el) => {
                    return el.userId == element.userId;
                  }).length > 0
                    ? 'add posts__like '
                    : ' posts__like '
                }
              >
                <div
                  onClick={(e) => handlerClick(false, e)}
                  className='posts__icon '
                >
                  <div className='posts__svg down'>
                    <Down />
                  </div>
                </div>
                <div className='posts__countlike'>
                  <p>{like.quantityDis}</p>
                </div>
              </div>
            </div>
            {('id' in user && +user.id == element.userId) ||
            ('id' in user && user.role == 'Admin') ? (
              <div className='posts__change'>
                <div
                  onClick={() => handlerBasket(element)}
                  className='posts__basket posts__svg'
                >
                  <Bucket />
                </div>
                {+user.id == element.userId && (
                  <div
                    onClick={() => setReload(true)}
                    className='posts__reload posts__svg'
                  >
                    <Reload />
                  </div>
                )}
              </div>
            ) : (
              ''
            )}
          </div>
        </div>
      ) : (
        <PostsItemReload
          item={element}
          setElement={setElement}
          setReload={setReload}
          rating={ratingValue}
          setRating={setRatingValue}
        />
      )}
    </>
  );
};

type PostTypes<T = any> = Pick<CommentsConf, 'model'> & T;

type ReviewTypes = Pick<CommentsConf, 'model'> & {
  deviceId?: RegExpMatchArray;
  item: TypesConfig;
  fetching: () => void;
};

type ElementType = {
  setElement: React.Dispatch<React.SetStateAction<TypesConfig>>;
  setReload: React.Dispatch<React.SetStateAction<boolean>>;
};

type ElementItemConf = {
  setRating: React.Dispatch<React.SetStateAction<number>>;
  rating: number;
};

export type Updatetypes = Omit<
  ElementType & Omit<ReviewTypes, 'model'> & ElementItemConf,
  'fetching'
>;
