import React from 'react';
import { Link } from 'react-router-dom';
import { Slider } from './Slider';
import { ReactComponent as Star } from '../../../../../img/icons/star.svg';
import { useAppDispatch, useAppSelector } from '../../../../../store';
import { ProductBtn, btnHOC, HeartBtn } from '../../../../../components';
import { changeModal } from '../../../../../store/modalSlice/modalSlice';
import { countItemBasket } from '../../../../Basket/components/basketFunction';
import { ProductT } from '../../../../../store/goodsSlices/goods.config';

interface TypeBlock {
  product: ProductT;
}

export const ItemProduct: React.FC<TypeBlock> = ({ product }) => {
  const Btn = btnHOC(ProductBtn);
  const dispatch = useAppDispatch();
  const { products } = useAppSelector((state) => state.basket);

  function open(com, sub) {
    dispatch(changeModal({ isActive: true, component: com, subCom: sub }));
  }

  return (
    <div className='product__item'>
      <div className='product__blockimage'>
        <Slider product={product} />
        <div className='product__heart'>
          <HeartBtn id={product.id} model={open} />
        </div>
        {product.sale > 0 && (
          <div className='product__sale'>
            <div className='product__saleback'>
              <div className='product__saleground'></div>
              <div className='product__saletext'>
                <p className='mini'>-{product.sale}%</p>
              </div>
            </div>
          </div>
        )}

        <div className='product__rating rating'>
          <div className='rating__body'>
            <div className='rating__star'>
              <Star />
            </div>
            <div className='rating__text'>
              <p className='mini'>{countLikes(product.ratings) || 0}</p>
            </div>
          </div>
        </div>
      </div>
      <div className='product__text'>
        <div
          className={product.sale > 0 ? 'product__price add' : 'product__price'}
        >
          <SaleProduct product={product} />
        </div>
        <div className='product__textbottom'>
          <div translate='no' className='product__name'>
            <p className='mini'>
              <span data-type-string>
                {product.name.length <= 30
                  ? product.name
                  : product.name
                      .split('')
                      .filter((_, index) => index <= 29)
                      .join('')}
                {product.name.length > 30 && (
                  <span
                    title={product.name
                      .split('')
                      .filter((_, index) => index > 29)
                      .join('')}
                  >
                    ...
                  </span>
                )}{' '}
                /{' '}
              </span>

              <span className='product__category '>
                {product?.brand?.name.length <= 30
                  ? product?.brand?.name
                  : product?.brand?.name
                      .split('')
                      .filter((item, index) => index <= 30)
                      .join('')}{' '}
                {product?.brand?.length > 30 && (
                  <span
                    title={product?.brand
                      .split('')
                      .filter((_, index) => index > 29)
                      .join('')}
                  >
                    ...
                  </span>
                )}
              </span>
            </p>
          </div>
        </div>
      </div>
      <Link
        to={`/shop/item/${product.id}`}
        onClick={() => {
          document.documentElement.scrollIntoView(true);
        }}
        className='product__viewmore'
      >
        <p className='big'>review</p>
      </Link>
      <Btn
        product={product}
        model={open}
        show={countItemBasket(products, product?.id || 0) > 0 ? true : false}
      />
    </div>
  );
};

export const SaleProduct: React.FC<TypeBlock> = ({ product }) => {
  return (
    <>
      {product.sale > 0 ? (
        <>
          <p className='big' data-type-number>
            $
            {(+product.price - (+product.price / 100) * product.sale)
              .toFixed(1)
              .replace(/\.0/, '')}
          </p>
          <p data-type-number>${product.price}</p>
        </>
      ) : (
        <>
          <p className='big' data-type-number>
            ${product.price}
          </p>
        </>
      )}
    </>
  );
};

export const countLikes = (product) => {
  if (product.length > 0) {
    let five = 0;
    let four = 0;
    let three = 0;
    let two = 0;
    let one = 0;

    product.map((item) => {
      switch (item.quantity) {
        case 5:
          five += 1;
          break;
        case 4:
          four += 1;
          break;
        case 3:
          three += 1;
          break;
        case 2:
          two += 1;
          break;
        case 1:
          one += 1;
          break;
      }
    });

    return +(
      (five * 5 + four * 4 + three * 3 + two * 2 + one) /
      product.length
    ).toFixed(0);
  }
};
