import { useEffect, useRef, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { StarsChange } from '../Start/StarDevice';
import { Updatetypes } from './POSTS';
import { BtnLoading, Loading } from '../../../../components';
import { useAppDispatch, useAppSelector } from '../../../../store';
import { changeCommentHTTP } from '../../../../store/http/commentHTTP';
import { changeRatingHTTP } from '../../../../store/goodsSlices/ratingSlice/ratingSlice';

export const PostsItemReload: React.FC<Updatetypes> = ({
  item,
  setElement,
  setReload,
  rating,
  setRating,
}) => {
  const deviceId = useLocation().pathname.match(/[0-9]*$/gm);
  const { user } = useAppSelector((state) => state.user);
  const { isLoading } = useAppSelector((state) => state.rating);
  const dispatch = useAppDispatch();
  const [value, setValue] = useState(item.text);
  const ref = useRef<HTMLTextAreaElement>(null!);

  useEffect(() => {
    ref.current.focus();
  });

  const handlerSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (deviceId && 'id' in user) {
      const response = await changeCommentHTTP({
        commentId: item.id,
        text: value,
      });
      await dispatch(
        changeRatingHTTP({
          userId: user.id,
          deviceId: deviceId[0],
          quantity: rating,
        })
      );

      if (response.message === 'Ok') {
        setElement(response.comment);
        setReload(false);
      }
    }
  };

  return (
    <div className='reload'>
      <form onSubmit={handlerSubmit}>
        <div className='reload__rating'>
          <StarsChange
            number={rating}
            setRating={setRating}
            text={'Your score on the product'}
          />
        </div>
        <div className='reoad__input'>
          <textarea
            ref={ref}
            name='reload'
            value={value}
            onChange={(e) => {
              setValue(e.target.value);
            }}
          />
        </div>
        <div className='reload__footer'>
          <button className='reload__btn'>
            {isLoading ? (
              <BtnLoading>
                <Loading />
              </BtnLoading>
            ) : (
              <p className='big'>UPDATE REVIEW</p>
            )}
          </button>
          <div className='reload__cancel' onClick={() => setReload(false)}>
            <p className='big'>Cancel</p>
          </div>
        </div>
      </form>
    </div>
  );
};
