import { ReactComponent as Star } from '../../../../img/icons/star.svg';
import React, { useEffect } from 'react';
import { countLikes } from '../../../Shop/Goods/components/ItemProduct/ItemProduct';
import { useAppSelector } from '../../../../store';

import { $, JQ } from '../../../../components';
import { numberRating } from './numberRating';

import './StarDevice.scss';

export const StarDevice: React.FC<StarTypes> = ({ text, setElem }) => {
  const { rating } = useAppSelector((state) => state.rating);
  const handleMouse = (e: React.MouseEvent) => {
    const target = (e.target as HTMLElement).closest(
      '.stars__item'
    ) as HTMLElement;

    if (e.type == 'mouseover') {
      addClass(target);
    } else if (e.type == 'mouseleave') {
      renderStar();
    }
  };

  const renderStar = () => {
    if (numberRating(rating) && numberRating(rating) > 0) {
      const element = $(`.stars__item:nth-child(${countLikes(rating)})`).take();
      addClass(element);
    } else {
      $('.stars__item').remove('add');
    }
  };

  useEffect(() => {
    renderStar();
  });

  const handleClick = () => {
    const target =
      $('.comments').take().getBoundingClientRect().top + window.pageYOffset;
    window.scrollTo({
      behavior: 'smooth',
      top: target,
    });
    setElem('ADD POST');
    $('.comments__item').remove('check');
    $('.comments__items .comments__item:last-child').add('check');
  };

  return (
    <div className='stars'>
      <div className='stars__block'>
        <div
          className='stars__items'
          onMouseOver={handleMouse}
          onMouseLeave={handleMouse}
          onClick={handleClick}
        >
          <div className='stars__item' onMouseOver={handleMouse}>
            <Star />
          </div>
          <div className='stars__item' onMouseOver={handleMouse}>
            <Star />
          </div>
          <div className='stars__item' onMouseOver={handleMouse}>
            <Star />
          </div>
          <div className='stars__item' onMouseOver={handleMouse}>
            <Star />
          </div>
          <div className='stars__item' onMouseOver={handleMouse}>
            <Star />
          </div>
        </div>

        <div className='stars__count'>
          <p className=''>{rating.length}</p>
        </div>
      </div>
      {rating.length <= 0 && (
        <div className='stars__text'>
          <p className='mini'>{text}</p>
        </div>
      )}
    </div>
  );
};

export const StarsChange: React.FC<StarChangeTypes> = ({
  text,
  number,
  setRating,
}) => {
  const handleMouse = (e: React.MouseEvent) => {
    const target = (e.target as HTMLElement).closest(
      '.starschange__item'
    ) as HTMLElement;

    if (e.type == 'mouseover') {
      addClass(target);
    } else if (e.type == 'mouseleave') {
      renderStar();
    }
  };

  const renderStar = () => {
    if (number && number > 0) {
      const element = $(`.starschange__item:nth-child(${number})`).take();
      addClass(element);
    } else {
      $('.starschange__item').remove('add');
    }
  };

  useEffect(() => {
    renderStar();
  });

  const handleClick = (e: React.MouseEvent) => {
    const target = (
      (e.target as HTMLElement).closest('.starschange__item') as HTMLElement
    ).dataset.num;

    if (target) setRating(+target);
  };

  return (
    <div className='starschange'>
      <div className='starschange__items' onMouseLeave={handleMouse}>
        <div
          data-num={1}
          className='starschange__item'
          onMouseOver={handleMouse}
          onClick={handleClick}
        >
          <Star />
        </div>
        <div
          data-num={2}
          className='starschange__item'
          onMouseOver={handleMouse}
          onClick={handleClick}
        >
          <Star />
        </div>
        <div
          data-num={3}
          className='starschange__item'
          onMouseOver={handleMouse}
          onClick={handleClick}
        >
          <Star />
        </div>
        <div
          data-num={4}
          className='starschange__item'
          onMouseOver={handleMouse}
          onClick={handleClick}
        >
          <Star />
        </div>
        <div
          data-num={5}
          className='starschange__item'
          onMouseOver={handleMouse}
          onClick={handleClick}
        >
          <Star />
        </div>
      </div>
      <div className='starschange__text'>
        <p className='mini'>{text}</p>
      </div>
    </div>
  );
};

const addClass = (element) => {
  ($(element).prev() as JQ).add('add');
  ($(element).next() as JQ).remove('add');
  $(element).add('add');
};

export const Stars: React.FC<StarConf> = ({ number }) => {
  const { rating } = useAppSelector((state) => state.rating);

  const renderStar = () => {
    if (numberRating(rating) && number) {
      const element = $(`.star__item:nth-child(${number})`).take();
      addClass(element);
    }
  };

  useEffect(() => {
    renderStar();
  });

  return (
    <div className='star'>
      <div className='star__items'>
        <div className='star__item'>
          <Star />
        </div>
        <div className='star__item'>
          <Star />
        </div>
        <div className='star__item'>
          <Star />
        </div>
        <div className='star__item'>
          <Star />
        </div>
        <div className='star__item'>
          <Star />
        </div>
      </div>
    </div>
  );
};

type StarConf = {
  number: number;
};

type StarTypes = {
  text: string;
  setElem: React.Dispatch<React.SetStateAction<string>>;
};

interface StarChangeTypes extends StarConf {
  text: string;
  setRating: React.Dispatch<React.SetStateAction<number>>;
}
